.events{
    background: $light;
    &__container{
        display: flex;
        position: relative;
        z-index: 1;
    }
    &__inner{
        display: flex;
        flex-direction: column;
    }
    &__back{
        display: flex;
        align-items: center;
        font-family: $secondaryFont;

        font-size: 15px;
        line-height: 18px;
        color: #F0F0ED;
        &-arrow{
            width: 55px;
            height: 55px;
            border-radius: 50%;
            border: 1px solid #F0F0F5;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 8px;
        }
        &-img{

        }
    }
    &-select{
        padding-top: 12px;
        padding-bottom: 37px;
        background: #1A1919;
        display: flex;
        &__big{
            list-style: none;
            display: flex;
            align-items: flex-end;
            justify-content: space-between;
            &-item{
                font-weight: bold;
                font-size: 27px;
                line-height: 77px;
                color: #FFFFFF;
                &--select{
                    font-style: normal;
                    font-weight: bold;
                    font-size: 27px;
                    line-height: 77px;
                    position: relative;
                    &::after{
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        content: "";
                        width: 100%;
                        height: 3px;
                        background: $gold;
                    }
                }
            }
        }
        &__small{
            list-style: none;
            display: flex;
            flex-wrap: wrap;
            margin-top: 16px;
            &-item{
                padding: 10px 20px;
                background: $dark;   
                border-radius: 1000px;
                color: $white;
                margin-right: 8px;
                margin-bottom: 11px;
                cursor: pointer;
                &--select{
                    background: $gold;
                }        
            }
        }
    }
    &-all{
        padding-bottom: 175px;
        position: relative;
        &::before{
            content: "";
            position: absolute;
            background: url('../img/products/logo.png');
            background-repeat: no-repeat;
            width: 1162px;
            height: 256px;
            top: 32px;
            left: 33%;
            z-index: 0;
        }
        &__list{
            display: flex;
            flex-wrap: wrap;
            list-style: none;
        }
        &__inner{
            padding-top: 111px;
        }
        
    }
    &-item{
        max-width: 208px;
        max-height: 371px;
        width: 100%;
        height: 100%;
        background: $white;
        // padding: 16px;
        margin-bottom: 12px;
        border-radius: 0 0 0 10px;
        &:not(:nth-child(4n+4)){
            margin-right: 12px;
        }
        &__img{
            // margin-bottom: 17px;
            border-radius: 0 0 0 10px;
        }
        &__link{
            font-size: 15px;
            line-height: 21px;
            text-decoration-line: underline;
            color: #1A1919;
            display: block;
            padding: 16px 16px 16px 16px;
        }
    }
}